
import  createGenresMarkup   from './create-genres-markup';

export default function trendMovieCardTmpl({ id, title, poster_path, genre_ids, release_date }) {

    //   
    let releaseYear = release_date.slice(0, 4);

    //  ,       
    const posterUrl = 'https://image.tmdb.org/t/p/w500/' + poster_path;

    //     
    let convertedGenres = createGenresMarkup(genre_ids);
   

    return `<li class="movies__item" data-id="${id}">
    <div class="movies__poster">
    <img src="${posterUrl}" alt="${title}" class="movies__image" />
    </div>
        <div class="movies__meta">
            <p class="movies__title">${title}</p>
            <p class="movies__info"><span class="movies__genre">${convertedGenres}</span> | <span class="movies__year">${releaseYear}</span></p>
        </div>
    </li>`;
};